.asksWrapper {
  margin: 180px auto 0 auto;
  position: relative;
  z-index: 1;

  h2 {
    max-width: 641px;
    margin: 0 auto 56px auto;
    text-align: center;
  }

  .asksContent {
    position: relative;
    display: flex;
    gap: 24px;
    min-height: 511px;

    .question {
      width: 496px;

      .askMark {
        position: absolute;
        top: -890px;
        left: -340px;
      }
    }

    .accordions {
      .accordionBlock {
        margin-bottom: 24px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

}